import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { alpha } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';

import { useDispatch, useSelector } from 'src/redux/store';
import { increment, decrement } from 'src/redux/slices/counter';

import { useSettingsContext } from 'src/components/settings';

// ----------------------------------------------------------------------

export default function BlankView() {
  const settings = useSettingsContext();

  const dispatch = useDispatch();

  const { value } = useSelector((state) => state.counter);

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Box
        sx={{
          mt: 5,
          width: 1,
          height: 320,
          borderRadius: 2,
          bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
          border: (theme) => `dashed 1px ${theme.palette.divider}`,
        }}
      >
        <Stack alignItems="center" justifyContent="center" direction="row" spacing={2} height={1}>
          <Button variant="contained" onClick={() => dispatch(increment())}>
            - Increment
          </Button>

          <Typography variant="h2">Counter: {value}</Typography>

          <Button variant="contained" onClick={() => dispatch(decrement())}>
            + Decrement
          </Button>
        </Stack>
      </Box>
    </Container>
  );
}
